<template>
  <x-modal
    class="vs-modal"
    v-bind="$props"
    @cancel="onCancel"
    @accept="onAccept"
    @close="onClose"
    :active="active"
    :parent="parent"
  >
    <slot></slot>
  </x-modal>
</template>

<script>
import XModal from './xmodal'
export default {
  name: "modal-wrapper",
  components: {
    XModal,
  },
  props: {
    color: {
      default: "primary",
      type: String
    },
    title: {
      default: "弹框",
      type: String
    },
    buttonAccept: {
      default: "filled",
      type: String
    },
    buttonCancel: {
      default: "flat",
      type: String
    },
    isValid: {
      default: "none",
      type: [Boolean, String]
    },
    buttonsHidden: {
      default: false,
      type: Boolean
    },
    acceptText: {
      default: "",
      type: String
    },
    cancelText: {
      default: "",
      type: String
    },
    iconPack: {
      default: "material-icons",
      type: String
    },
    closeIcon: {
      default: "close",
      type: String
    },
    cancel: {
      default: () => {},
      type: Function
    },
    accept: {
      default: () => {},
      type: Function
    },
    width: {
      type: [String, Number],
      default: 826,
    }
  },
  data() {
    return { active: true };
  },
  watch: {
    active(n) {
      if (!n) {
        this.$modalClose(this.modalid);
        this.$emit("close");
      }
    }
  },
  computed: {
    modalid() {
      return this.$parent.id;
    },
    parent() {
      // return this.$parent.$el
      return document.getElementById('modal-container')
    }
  },
  methods: {
    onClose() {
      return this.onCancel();
    },
    async onAccept() {
      const rt = await this.accept();
      if (rt !== false) {
        this.active = false;
      }
    },
    async onCancel() {
      const rt = await this.cancel();
      if (rt !== false) {
        this.active = false;
      }
    }
  }
};
</script>